@import url(https://fonts.googleapis.com/css2?family=Shadows+Into+Light&display=swap);
:root {
  --primary: #DB1A20;
  --secondary: #AAB1AE;
  --tertiary: #A30E00;
  --light: #fafafa;
  --black: #333333;
  --error: rgb(228, 46, 1);
  --spacing-one: calc(1rem * 0.5);
  --spacing-two: calc(1rem);
}

* {
  box-sizing: border-box;
}

html,
body {
  min-height: 100%;
}

body {
  margin: 0;
  padding: 0;
  font-family: 'Karla', sans-serif;
  font-weight: 400;
  line-height: 1.5;
  font-size: 20px;
  color: #fafafa;
  color: var(--light);
}

h1,
h2,
h3,
h4,
h5,
h6,
footer {
  font-family: 'Rubik', sans-serif;
  font-weight: 500;
  margin: 5px 0;
  line-height: 1.25;
}

h1 {
  font-size: 2.25rem;
}

h2 {
  font-size: 2rem;
}

h3 {
  font-size: 1.75rem;
}

h4 {
  font-size: 1.5rem;
}

h5 {
  font-size: 1.25rem;
}

h6 {
  font-size: 1rem;
}

p {
  font-size: 1.1rem;
  margin-top: 0;
}

a {
  color: #A30E00;
  color: var(--tertiary);
  text-decoration: none;
  font-weight: bold;
}

a:hover {
  text-decoration: underline;
}

header {
  background: #DB1A20;
  background: var(--primary);
}

header a {
  color: #fafafa;
  color: var(--light);
}

button {
  font-size: 1rem;
  text-decoration: none;
  padding: 0.25rem 1rem;
  border-radius: .5rem;
  border: none;
  outline: none;
  cursor: pointer;
  margin: 0 1px;
  background-color: #A30E00;
  background-color: var(--tertiary);
}

button:hover {
  opacity: .8;
}

button[disabled] {
  cursor: default;
  opacity: .8;
}

img {
  max-width: 100%;
}

span[role="img"] {
  cursor: default;
}

form {
  width: 40%;
}

input {
  font-size: 1rem;
  padding: 0.25rem;
  border: 1px solid #333333;
  border: 1px solid var(--black);
  outline: none;
  border-radius: 3px;
  transition: box-shadow 0.2s;
}

input:focus {
  border: 1px solid #A30E00;
  border: 1px solid var(--tertiary);
  box-shadow: 0 0 .25rem rgba(0, 0, 0, .5);
}

.container {
  width: 85%;
  max-width: 1200px;
  margin-left: auto;
  margin-right: auto;
}

.flex-row {
  display: flex;
  flex-wrap: wrap;
}

.space-between {
  justify-content: space-between;
}

.flex-end {
  justify-content: flex-end;
}

.mx-1 {
  margin-right: calc(1rem * 0.5);
  margin-right: var(--spacing-one);
  margin-left: calc(1rem * 0.5);
  margin-left: var(--spacing-one);
}

.mx-2 {
  margin-right: calc(1rem);
  margin-right: var(--spacing-two);
  margin-left: calc(1rem);
  margin-left: var(--spacing-two);
}

.my-1 {
  margin-top: calc(1rem * 0.5);
  margin-top: var(--spacing-one);
  margin-bottom: calc(1rem * 0.5);
  margin-bottom: var(--spacing-one);
}

.my-2 {
  margin-top: calc(1rem);
  margin-top: var(--spacing-two);
  margin-bottom: calc(1rem);
  margin-bottom: var(--spacing-two);
}

.px-1 {
  padding-right: calc(1rem * 0.5);
  padding-right: var(--spacing-one);
  padding-left: calc(1rem * 0.5);
  padding-left: var(--spacing-one);
}

.px-2 {
  padding-right: calc(1rem);
  padding-right: var(--spacing-two);
  padding-left: calc(1rem);
  padding-left: var(--spacing-two);
}

.py-1 {
  padding-top: calc(1rem * 0.5);
  padding-top: var(--spacing-one);
  padding-bottom: calc(1rem * 0.5);
  padding-bottom: var(--spacing-one);
}

.py-2 {
  padding-top: calc(1rem);
  padding-top: var(--spacing-two);
  padding-bottom: calc(1rem);
  padding-bottom: var(--spacing-two);
}

.card {
  width: 25%;
  text-align: center;
}

.error-text {
  color: rgb(228, 46, 1);
  color: var(--error);
}

@media screen and (max-width: 768px) {
  .card {
    width: 100%;
  }
}
h1 {
  font-family: 'Shadows Into Light';
  box-sizing: border-box;
}

.top-nav {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    background-color: #333333;
    background: linear-gradient(to left, #AAB1AE, #DB1A20 );
    color: #FFF;
    height: 50px;
    padding: 1em;
  }

  .menu {
    display: flex;
    flex-direction: row;
    list-style-type: none;
    margin: 0;
    padding: 0;
  }
  .menu > li {
    margin: 0 1rem;
    overflow: hidden;
  }

  .menu-button-container {
    display: none;
    height: 100%;
    width: 30px;
    cursor: pointer;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  
  #menu-toggle {
    display: none;
  }
  
  .menu-button,
  .menu-button::before,
  .menu-button::after {
    display: block;
    background-color: #fff;
    position: absolute;
    height: 4px;
    width: 30px;
    transition: -webkit-transform 400ms cubic-bezier(0.23, 1, 0.32, 1);
    transition: transform 400ms cubic-bezier(0.23, 1, 0.32, 1);
    transition: transform 400ms cubic-bezier(0.23, 1, 0.32, 1), -webkit-transform 400ms cubic-bezier(0.23, 1, 0.32, 1);
    border-radius: 2px;
  }
  
  .menu-button::before {
    content: '';
    margin-top: -8px;
  }
  
  .menu-button::after {
    content: '';
    margin-top: 8px;
  }
  
  #menu-toggle:checked + .menu-button-container .menu-button::before {
    margin-top: 0px;
    -webkit-transform: rotate(405deg);
            transform: rotate(405deg);
  }
  
  #menu-toggle:checked + .menu-button-container .menu-button {
    background: rgba(255, 255, 255, 0);
  }
  
  #menu-toggle:checked + .menu-button-container .menu-button::after {
    margin-top: 0px;
    -webkit-transform: rotate(-405deg);
            transform: rotate(-405deg);
  }
  
  @media (max-width: 700px) {
    .menu-button-container {
      display: flex;
    }
    .menu {
      position: absolute;
      top: 0;
      margin-top: 50px;
      left: 0;
      flex-direction: column;
      width: 100%;
      justify-content: center;
      align-items: center;
    }
    #menu-toggle ~ .menu li {
      height: 0;
      margin: 0;
      padding: 0;
      border: 0;
      transition: height 400ms cubic-bezier(0.23, 1, 0.32, 1);
    }
    #menu-toggle:checked ~ .menu li {
      border: 1px solid #333;
      height: 2.5em;
      padding: 0.5em;
      transition: height 400ms cubic-bezier(0.23, 1, 0.32, 1);
    }
    .menu > li {
      display: flex;
      justify-content: center;
      margin: 0;
      padding: 0.5em 0;
      width: 100%;
      color: white;
      background-color: #222;
    }
    .menu > li:not(:last-child) {
      border-bottom: 1px solid #444;
    }
  }
body {
    background-color: var(--black);
}
.footer{
    margin-top: 1rem;
    padding: 1rem;
    background: linear-gradient(to right, #AAB1AE, #DB1A20 );
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100%;
  }

.footer-card{ 
    border-radius: 25px;
    background: #333333;
    padding: 20px;
    width: 200px;
    height: 70px;
}
.w-xsm {
    width: 11%;
}

